<template>
  <div id="login">
  <h4>用户名</h4>
  <input type="text" v-model="username" placeholder="用户名"  >
  <h4>密码</h4>
  <input type="text" v-model="password" placeholder="密码"  @keyup.enter="onLogin">
    <el-button size="medium" @click="onLogin">立即登录</el-button>
    <p>没有账号？<router-link to="/register">注册新用户</router-link></p>
  </div>
</template>

<script>
import {mapActions, mapAtions,mapMutations}  from 'vuex'

export default {
name: '',
  data () {
    return {
      username:'',
      password:''
    }
  },
  components: { },
  methods:{
    ...mapActions(['login']),
    onLogin() {
      this.login({username:this.username,password:this.password})
      .then(() => {
     this.$message.success('登录成功')
        this.$router.push({path: this.$route.query.redirect ||'/'})  ///
      })
    }
  }
}
</script>

<style lang="less" scoped>

/* @themeColor: rgb(0, 153, 0);
@themeLighterColor: rgba(0, 153, 51, 0.6);
@bgColor: #149739;
@textLighterColor: #999; */

#login {
  display: grid;
  justify-content: center;
  padding-top: 30px;
  
  h4 {
    margin: 10px 0 5px;
  }

  p {
    margin: 5px 0;
    text-align: center;
   
    a {
      color: rgb(0, 153, 0);
    }
  }

  input {
    width: 400px;
  }

  .error {
    font-size: 12px;
    color: #f00;
  }

  button {
    margin-top: 30px;
    justify-self: start;
  }

  .notice {
    font-size: 12px;
    color:#999;
    text-align: center;
    margin-top: 30px;

    a {
      color:rgb(0, 153, 0);
    
  }
  }


// a {
//   text-decoration: none;
//    color: #333;
// }

// .pagination {
//   display: grid;
//   justify-items: center;
//   margin-bottom: 30px;
// }





.el-button {
  color: rgb(0, 153, 0);
  border: 1px solid rgba(0, 153, 51, 0.6);
  background: #fff;
  border-radius: 4px;  
}

.el-button.el-button--medium{
  font-size: 13px;
  padding: 0 8px;
  font-weight: normal;
  line-height: 28px;
}


.el-button:hover {
  color: #fff;
  background-color:rgba(0, 153, 51, 0.6) ;
}



input {
  line-height: 40px;
  border: 1px solid #eaeaea;
  padding: 0 10px;
  border-radius: 4px;
  outline: none;
}
input:focus {
  border-color: rgba(0, 153, 51, 0.6);
}

.el-input.is-active .el-input__inner, .el-input__inner:focus, .el-textarea__inner:focus {
  border-color: rgba(0, 153, 51, 0.6);
}
}



</style>



