<template>
  <div id="index">
   <section class="blog-posts">
    <router-link class="item"  v-for="blog in blogs" :to="`/detail/${blog.id}`"  :key="blog.id">
      <figure class="avatar">
      <img :src="blog.user.avatar" :alt="blog.user.username">
      <figcaption>{{blog.user.username}}</figcaption>
      </figure>
      <h3>{{blog.title}} <span>{{blog.createdAt | formatDate}}</span></h3> 
      <p>
        {{blog.description}} 
      </p>
    </router-link>
   </section>
   <section class="pages">
      <el-pagination 
    layout="prev, pager, next"
    @current-change="onPage"
    :total="total">
    
  </el-pagination>
   </section>
  </div>
</template>

<script>
import blogs from '@/apis/blogs'
export default {
name: '',
  data () {
    return {
      blogs:[],
      total:0,
      page:1
    }
  },
  created(){
    this.page = parseInt(this.$route.query.page)
  blogs.getIndexBlogs({page:this.page}) 
  .then(res => {
    this.blogs = res.data
    this.total = res.total
    this.page = res.page

  })
  },
  methods:{
   onPage(newPage) {
      blogs.getIndexBlogs({page:newPage}) 
  .then(res => {
    this.blogs = res.data
    this.total = res.total
    this.page = res.page
    this.$router.push({path:'/',query:{page:newPage}})
  })
   }
  },
  components: { },
}
</script>

<style  lang="less">
#index {
  .item {
  display: grid;
  grid: auto  auto  /  80px  1fr  ;
  margin: 20px 0px;
  
.avatar{
  grid-column: 1;
  grid-row: 1 / span 2;
  margin-left: 0;
  text-align: center;
  justify-self: center;
}
img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
figcaption {
  font-size: 12px;
  color:#999 ;
}
  }
  h3 {
    grid-column: 2;
    grid-row: 1;
 
    &>span {
      color: #999;
      //display:inline-block;
      font-size: 12px;
      font-weight: normal;  //字体粗细正常
    
    }
  }
   p {
     grid-column: 2;
     grid-row: 2;
     margin-top: 0;
     //white-space:normal
      text-overflow: ellipsis;  //溢出隐藏
      overflow: hidden;
   }
}
.pages {
 display: grid;
justify-items: center;
margin-bottom: 20px;
}
</style>



