<template>
  <div id="register">

  <h4>用户名</h4>
  <input type="text" v-model="username" placeholder="用户名">
  <h4>密码</h4>
  <input type="text" v-model="password" placeholder="密码" @keydown.enter="onRegister">
    <el-button size="medium" @click="onRegister">立即注册</el-button>
    
    <p>已有账号？<router-link to="/login">立即登录</router-link></p>

  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
name: '',
  data () {
    return {
      username:'',
      password:''
    }
  },
  methods:{
    ...mapActions(['register']),
   onRegister() {
     this.register({username:this.username,password:this.password})
     .then(() => {
     this.$message.success('注册成功')
     this.$router.push({path:'/'})

     })
   }
 


  },
  components: { },
}
</script>

<style  lang="less" scoped>

 #register {
  display: grid;
  justify-content: center;
  padding-top: 30px;
  
  h4 {
    margin: 10px 0 5px;
  }

  p {
    margin: 5px 0;
      text-align: center;
   
    a {
      color: rgb(0, 153, 0);
    }
  }

  input {
    width: 400px;
  }

  .error {
    font-size: 12px;
    color: #f00;
  }

  button {
    margin-top: 30px;
    justify-self: start;
  }

  .notice {
    font-size: 12px;
    color:#999;
    text-align: center;
    margin-top: 30px;

    a {
      color:rgb(0, 153, 0);
    
  }
  }


// a {
//   text-decoration: none;
//    color: #333;
// }

// .pagination {
//   display: grid;
//   justify-items: center;
//   margin-bottom: 30px;
// }





.el-button {
  color: rgb(0, 153, 0);
  border: 1px solid rgba(0, 153, 51, 0.6);
  background: #fff;
  border-radius: 4px;  
}

.el-button.el-button--medium{
  font-size: 13px;
  padding: 0 8px;
  font-weight: normal;
  line-height: 28px;
}


.el-button:hover {
  color: #fff;
  background-color:rgba(0, 153, 51, 0.6) ;
}



input {
  line-height: 40px;
  border: 1px solid #eaeaea;
  padding: 0 10px;
  border-radius: 4px;
  outline: none;
}
input:focus {
  border-color: rgba(0, 153, 51, 0.6);
}

.el-input.is-active .el-input__inner, .el-input__inner:focus, .el-textarea__inner:focus {
  border-color: rgba(0, 153, 51, 0.6);
}
}


</style>




