<template>
  <div id="app">
    <Header id="header"/>
    <main id="main">
   <router-view/>
    </main>
   <Footer id="footer"/>

  </div>
</template>

<script>
import Footer from './components/footer.vue'
import Header from './components/header.vue'



export default {
  components: {Header, Footer },
  name: 'App'
}
</script>

<style lang="less">

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.el-button {
  font-size: 13px;
  color: #006600;
  height: 28px;
  line-height: 28px;
  padding: 0 8px;
  border-radius: 4px;
  font-weight: normal;
  border: none;
}


html,body,#app {
  height: 100%;
}
#app {
 display: grid;
 grid-template-columns: 12% auto 12%;
 grid-template-rows: auto 1fr auto;
 grid-template-areas: "header  header  header "
                      " .      main    ."
                      "footer  footer  footer ";
}
#header {
  grid-area: header;
  padding-left: 12%;
  padding-right: 12%;
}
#main {
  grid-area: main;
}
#footer {
  grid-area: footer;
  padding-right: 12%;
  padding-left: 12%;
}

@media (max-width:768px) {
  #app {
    grid-auto-columns: 10px auto 10px;
    #header,#footer {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}


</style>



