<template>
  <div id="detail">
  <section class="user-info">
    <img :src="user.avatar" :alt="user.username" :title="user.title">
    <h3>{{title}}</h3>
    <p><router-link :to="`/user/${user.id}`">{{user.username}}</router-link>发布于：{{friendlyDate(createdAt)}}</p>
  </section>
  <section class="article" v-html="markdown">
  </section>
  </div>
</template>

<script>
import {marked} from 'marked'

import blogs from '@/apis/blogs'
console.log(marked)
export default {
name: '',
  data () {
    return {
      user:{},
      createdAt:'',
      title:'',
      rawContent:''
    }
  },
    created() {
  this.blogId = this.$route.params.blogId
  blogs.getDetail({blogId:this.blogId})
  .then(res => {
    this.title = res.data.title
    this.user = res.data.user
    this.createdAt = res.data.createdAt
    this.rawContent = res.data.content
  })
  },
  computed: { 
    markdown(){
     // console.log(this.rawContent)
      //console.log(marked)
      return   marked(this.rawContent)
    }
  }

}
</script>

<style   lang="less">

#detail {
  .user-info {
    display: grid;
    grid: auto auto / 80px 1fr;

    margin-top: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ededed;
  .avatar {
    grid-column: 1;
    grid-row: 1 / span 2;
    width: 60px;
    height: 60px;
    border-radius: 50%;
  }
    h3 {
      grid-column: 2;
      grid-row: 1;
      margin: 5px 0;
    }
  p {
    grid-column: 2;
    grid-row: 2;
    margin-top: 0;
    font-size: 12px;
    color: #999;
    font-size: 15px;
  a{
    color:rgb(0, 153, 0) ;
    text-decoration: none;
    font-size: 15px;
    margin-right: 10px;
    font-weight: bold;
  }

  }
}
.article {
  padding: 30px 0;
}
}




.article {
  h1 {
    margin: 0 0 1em;
  }

  h2 {
    margin: 45px 0 0.8em;
    padding-bottom: 0.7em;
    border-bottom: 1px solid #ddd;
  }

  h3 {
    margin: 52px 0 1.2em;
  }

  figure,
  p,
  ul,
  ol {
    margin: 1.2em 0;
  }

  p,
  ul,
  ol {
    line-height: 1.6em;
  }

  ul,
  ol {
    padding-left: 1.5em;
  }

  a {
    color: #42b983;
    font-weight: 600;
  }

  blockquote {
    margin: 2em 0;
    padding-left: 20px;
    border-left: 4px solid #42b983;
  }

  blockquote p {
    font-weight: 600;
    margin-left: 0;
  }

  iframe {
    margin: 1em 0;
  }
}


</style>



