import request from '@/helpers/request'
const URL = {
  GET_LIST :'/blog',  //获取博客列表
  GET_DETAIL : '/blog/:blogId', //获取博客详情
  CREATE:'/blog',  //创建
  UPDATE:'/blog/:blogId',  
  DELETE:'/blog/:blogId'  //删除
}

export default {
  getBlogs({page=1,userId,atIndex} = {page:1}) {
      return request(URL.GET_LIST,'GET',{page,userId,atIndex})
  },
  getIndexBlogs({page=1} = {page :1}) {
      return this.getBlogs({page,atIndex:true})   //获取置顶博客
  },
  getBlogsByUserId(userId,{page=1,atIndex} = {page:1}) {
      return this.getBlogs({userId,page,atIndex})
  },
  getDetail({blogId}) {
      return request(URL.GET_DETAIL.replace(':blogId',blogId))
  },
  updateBlog({blogId},{title,content,description,atIndex}) {
      return request(URL.UPDATE.replace(':blogId',blogId),'PATCH',{content,title,description,atIndex})
  },
  createBlog({title='',content='',description='',atIndex=false} = {title:'',content:'',description:'',atIndex:false}) {  //默认参数，传了就用=，没传就用：''
      return request(URL.CREATE,'POST',{title,content,description,atIndex})
  },
  deleteblog({blogId}){
      return request(URL.DELETE.replace(':blogId',blogId),'DELETE')
  }

}


