import axios from 'axios'
import { Message } from 'element-ui'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.baseURL = '//blog-server.hunger-valley.com'
//axios.defaults.withCredentials = true  //表示是否使用跨域请求

export default function request(url,type='GET',data={}) {
    return new Promise((resolve,reject) => {
        let option = {
            url,method: type,
        }
    if(type.toLowerCase() === 'get') {
        option.params = data
    }else{
        option.data = data
    }
    if(localStorage.token){ //第一次发请求没有token,再发请求时，localstorage里有token,然后发给服务器做验证
        axios.defaults.headers.common['Authorization'] = localStorage.token //设置请求头里有个字段[Authorization]，把token放到里面发出去
    }
    axios(option).then(res => {
        console.log(res.data)
        if(res.data.status === 'ok'){
         if(res.data.token){  //如果请求里有token 
             localStorage.token = res.data.token  //就把token 放到localstorage(本地存储)里，下次发请求自动带上
         }
         resolve(res.data)
        }else{
            Message.error(res.data.msg)
            reject(res.data)
        }
    }).catch(err => {
        console.log(err)
        reject({msg:'网络错误'})
     Message.error('网络错误')
    })
    })
}


//测试注册接口   request('/auth/register/','post',{username:'abc',password:'123456'}).then(data => {console.log(data)})


