<template>
  <header :class="{login: isLogin, 'no-login': !isLogin}">
    <template v-if="!isLogin">
      <h1>Let's share</h1>
      <p>精品博客汇聚</p>
      <div class="btns">
        <router-link to="/login"><button >立即登录</button></router-link>
      <!-- <el-button>注册账号</el-button> -->
        <router-link to="/register"><button>注册账号</button></router-link>
      </div> 
    </template>
    <template v-if="isLogin">
      <h1><router-link to="/">Let's share</router-link></h1>
      <router-link to="/create"><i class="edit el-icon-plus"></i></router-link>
      <div class="user">
        <img class="avatar" :src="user.avatar" :alt="user.username" :title="user.username" >
        <ul>
          <li><router-link to="/my">我的</router-link></li>
          <li><a href="#"  @click="onLogout">注销</a></li>
        </ul>
      </div>         
    </template>
  </header>
</template>

<script>
  import {mapGetters,mapActions} from 'vuex'
  import auth from '@/apis/auth'
  window.auth = auth
import {Message} from 'element-ui'


  export default {
    data() {
      return {
          
      }
     },
    created() {
     this.checkLogin()
    },
    computed:{
      ...mapGetters(['user','isLogin'])
    },

    methods:{
      ...mapActions(['checkLogin','logout']),
     onLogout(){
    this.logout()
    Message.success('注销成功')
     } 
 
    }



  }
</script>


<style lang="less">

a {
  text-decoration: none;
   color: #333;
}

// .pagination {
//   display: grid;
//   justify-items: center;
//   margin-bottom: 30px;
// }


header.no-login {
  padding: 0 12% 30px 12%;
  background: #149739;
  display: grid;
  justify-items: center;

  h1 {
    color: #fff;
    font-size: 40px;
    margin: 60px 0 0 0;
    text-transform: uppercase;
  }

  p {
    margin: 15px 0 0 0;
    color: #fff;
  }
  
  .btns {
    margin-top: 20px;
  }

  button {
    margin: 20px 6px 0;
    padding: 8px;
    border-radius: 6px;
    border: none;
    cursor: pointer;
    &:hover {
      color:#149739 ;
    }
  }
} 


header.login {
  display: flex;
  align-items: center;
  background: #149739;

  h1 {
    margin: 0;
    padding: 0;
    font-size: 40px;
    text-transform: uppercase;
    flex: 1;

    a {
      color: #fff;
    }
  }

  .edit {
    color: #fff;
    font-size: 30px;
  }

  .avatar {
    width: 40px;
    height: 40px;
    border: 1px solid #fff;
    border-radius: 50%;
    margin-left: 15px;
  }

  .user {
    position: relative;

    ul {
      display: none;
      position: absolute;
      right: 0;
      list-style: none;
      border: 1px solid #eaeaea;
      margin:0;
      padding: 0;
      background-color: #fff;

      a {
         text-decoration: none;
        color: #333;
        font-size: 12px;
        display: block;
        padding: 5px 10px;

        &:hover {
          background-color: #eaeaea;
        }
      }

    }

    &:hover ul {
      display: block;
    }
  }
}



</style>


