<template>
  <div id="edit">
  <div class="wen">
    <h1>编辑文章</h1>
    <h3>文章标题</h3>
<el-input type="text" v-model="title"></el-input>
  </div>
  <div class="nei">
     <h3>内容简介</h3>
    <!-- <textarea name="" id="" cols="160" rows="5" v-model="description"></textarea> -->
    <el-input type="textarea" v-model="description"  :autosize="{ minRows: 2, maxRows: 6}"></el-input>
  </div>
  <div class="bo">
     <h3>博客内容</h3>
   <!-- <textarea name="" id="" cols="160" rows="15" v-model="content"></textarea> -->
    <el-input type="textarea" v-model="content" :autosize="{ minRows: 4, maxRows: 30}"></el-input>
   <p>
  <el-switch
  v-model="atIndex"
  active-color="#13ce66"
  inactive-color="#ff4949"></el-switch>是否展示到首页

   </p>
   <button @click="onEdit">确定</button>
  </div>
  </div>
</template>

<script>
import blogs from '@/apis/blogs'
export default {
name: '',
  data () {
    return {
      blogId:null,
      title:'',
      content:'',
      description:'',
      atIndex:false
    }
  },
  components: { },
created() {
  this.blogId = this.$route.params.blogId
blogs.getDetail({blogId:this.blogId})
.then(res => {
  this.title = res.data.title
  this.content = res.data.content
  this.description = res.data.description
  this.atIndex = res.data.atIndex
})
},
 

  methods:{
  onEdit() {
 blogs.updateBlog({blogId:this.blogId},{title:this.title,content:this.content,description:this.description,atIndex:this.atIndex})
 .then(res => {
   this.$message.success(res.msg)
   this.$router.push({path:`/detail/${res.data.id}`})
 })
  }
  }
}
</script>

<style lang="less" scoped>
h1 {
  
  text-align: center;
  margin-top: 10px;
}
input {
  width: 1170px;
  line-height: 30px;
 border: 1px solid #eaeaea;
 border-radius: 4px;
   outline: none;
}

input:focus {
  border-color: rgba(0, 153, 51, 0.6);
}
.wen h3 {
  margin-bottom: 10px;
}
.wen p {
  float: right;
}
.nei h3 {
  margin-top: 20px;
  margin-bottom: 10px;
}
.nei p {
  float: right;
}
.bo h3 {
  margin-top: 20px;
  margin-bottom: 10px;
}
.bo button {

  color: rgb(0, 153, 0);
  border: 1px solid rgba(0, 153, 51, 0.6);
  background: #fff;
  border-radius: 4px;  
  font-size: 13px;
  padding: 0 8px;
  font-weight: normal;
  line-height: 28px;
  &:hover {
  color: #fff;
  background-color:rgba(0, 153, 51, 0.6) ;
 
}
} 
.bo p {
  margin-top: 10px;
  margin-bottom: 10px;
}
.bo .el-switch {
  margin-right: 5px;
}
</style>



